.app {
  max-width: 1000px;
  margin: 0 auto;
}

.content {
  display: flex;
  padding: 10px;
}

.detail {
  flex: 1 1 70%;
}

.list {
  flex: 1 1 30%;
}

@media screen and (max-width: 640px) {
	.content {
		flex-wrap: wrap;
	}

  .detail {
    flex: 1;
  }

  .list {
    flex: 1;
  }
}